body {
    margin: 0;
    height: 100%;
    font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding: 20px;
    box-sizing: border-box;
}

.game-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: space-between;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
}

.team-panel {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 8px;
    width: 30%;
    box-sizing: border-box;
}

.blue-team {
    background-color: #e3f2fd;
    border: 2px solid #115aa4;
}

.red-team {
    background-color: #ffebee;
    border: 2px solid #9A1750;
}

.turn-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
    box-sizing: border-box;
}

.turn-indicator {
    font-size: 1.5em;
    margin-bottom: 10px;
    text-align: center;
}

.end-turn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}

.grid {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(5, 1fr);
    gap: 1vh;
    height: 75vh;
    width: 75vh;
    margin: auto;
}

.grid-cell {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s;
    text-align: center;
    font-size: 1.5vh;
}

.grid-cell.gray {
    background-color: #c0c0c0;
}

.grid-cell.blue {
    background-color: #115aa4;
    color: #ffffff;
}

.grid-cell.red {
    background-color: #9A1750;
    color: #ffbedb;
}

.grid-cell.white {
    background-color: #ffffff;
    color: black;
}

.grid-cell.black {
    background-color: #000;
    color: white;
}

.grid-cell.revealed {
    opacity: 0.7;
}

@media (max-width: 800px) {
    .grid {
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(25, 1fr);
        height: 100vh;
        width: auto;
    }

    .grid-cell {
        height: auto;
        width: 100%;
    }

    .team-panel {
        display: none;
    }

    .turn-info {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .turn-indicator, .end-turn {
        width: 100%;
        text-align: center;
        margin-bottom: 10px;
    }
}
